import BootPay from "bootpay-js";
import React, { useContext, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import styled from "styled-components";
import { checkMobile } from "../../App";
import icon_back from "../../assets/icon-back-arrow.svg";
import auth_duck from "../../assets/ic_signup_duck@2x.png";
import yellow_duck from "../../assets/ic_signup_little_duck (non-optimized)@4x.png";
import BottomButton from "../../components/party/BottomButton";
import { PageTransContext } from "../../containers/pageTransContext";
import { BottomNavCloseAction } from "../../reducers/container/bottomNav";
import { LoadingCloseAction, LoadingOpenAction } from "../../reducers/container/loading";
import { MessageClose, MessageOpen, MessageWrapClose, MessageWrapOpen } from "../../reducers/container/message";
import { customApiClient } from "../../shared/apiClient";
import { TextMiddle } from "../../styled/shared";
import { MainText } from "../../styled/shared/text";
import { ContentWrap, HeaderWrap, PageWrap } from "../../styled/shared/wrap";

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const RealNameAuth = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  let query = useQuery();

  //context
  const { setPageTrans } = useContext(PageTransContext);

  const closePage = () => {
    setPageTrans("trans toLeft");
    history.goBack();
  };

  useEffect(async () => {

    //  
    const isAuth = localStorage.getItem("isAuth");

    if (isAuth === "Y") {
      const pastPath = sessionStorage.getItem("pastPath");
      setPageTrans('trans toLeft');

      if (!pastPath || pastPath.length < 0) {
        history.push("/party");
      }
      else {
        history.push(pastPath);
      }

      return
    }

    dispatch(BottomNavCloseAction);

    const userPlatform = checkMobile();
    if (userPlatform == 'ios') {
      //IOS  
      try {
        window.webkit.messageHandlers.setColorWhite.postMessage("hihi");
      }
      catch (err) {
      }
    }
  }, []);

  // 
  const handleClickRealNameAuth = () => {

    BootPay.request({
      price: 0, // 0  .
      application_id: process.env.REACT_APP_BOOTPAY_ID,
      name: ' ', //  
      pg: 'danal',
      method: 'auth', //     
      show_agree_window: 0, //      
      order_id: (new Date()).getTime(), // ,    .
      user_info: {
        phone: localStorage.getItem("phone") || null
      },
    }).error(function (data) {
      //     .
      console.log(data);
    }).cancel(function (data) {
      //   .
      console.log(data);
    }).done(function (data) {
      //     .
      const receiptId = data.receipt_id;
      postRealNameAuth(receiptId);
    });
  }

  const postRealNameAuth = async (receiptId) => {

    dispatch(LoadingOpenAction);

    const data = await customApiClient('post', '/user/realName/auth', {
      receiptId: receiptId
    });

    //
    if (!data) {
      dispatch(LoadingCloseAction);
      alert(" .   .");
      return
    }

    //
    if (data.statusCode != 200) {
      dispatch(LoadingCloseAction);
      alert(data.message);
      return
    }

    //    isAdult 

    localStorage.setItem("isAuth", "Y");
    dispatch(LoadingCloseAction);

    // 
    //   
    dispatch({
      type: MessageWrapOpen
    })
    dispatch({
      type: MessageOpen,
      data: '  .'
    })

    setTimeout(() => {
      dispatch({
        type: MessageClose
      })
    }, 2000);
    setTimeout(() => {
      dispatch({
        type: MessageWrapClose
      })
    }, 2300);

    //   
    setPageTrans("trans toRight");

    if (query.get("path")) {
      history.push(query.get("path"));
    }
    else {
      history.push("/main");
    }
  }

  return (
    <div className="page">
      <PageWrap>
        <HeaderWrap id="back_link" className="spoqaBold" onClick={closePage}>
          <div
            style={{
              position: "absolute",
              top: "55%",
              left: "1.25rem",
              transform: "translate(0,-55%)",
            }}
          >
            <img src={icon_back}></img>
          </div>
          <TextMiddle></TextMiddle>
        </HeaderWrap>
        <ContentWrap>
          <MainText>
              
            <br />  1 <span className="yellowText"></span> .
          </MainText>
          <AuthContentWrap>
            <div style={{ textAlign: "center" }}>
              <img src={auth_duck} className="mainImg" />
            </div>
            <div className="notice">
              <div className="notice-wrap">
                <img src={yellow_duck} className="notice-img" />
                <div className="notice-text notoMedium">
                          1  .
                </div>
              </div>
            </div>
          </AuthContentWrap>
          <BottomButton
            text={""}
            activeStatus={true}
            isBottomStatus={true}
            clickFunc={handleClickRealNameAuth} />
        </ContentWrap>
      </PageWrap>
    </div>
  );
};

export default RealNameAuth;

const AuthContentWrap = styled.div`
  display: flex;
  flex-direction: column;
  /* align-items: center; */

  .mainImg {
    width: 15.4812rem;
    height: 8.3375rem;

    margin-top:2.5rem;
    margin-bottom: 0.8187rem;
  }

  .notice {
    height: 4.3125rem;
    display: flex;
    padding:0 0.9375rem;
    align-items: center;
    border-radius: 0.4375rem;
    background-color: #fff7e0;
  }

  .notice-wrap{
    display: flex;
    flex-direction: row;
  }

  .notice-img {
    width: 1.2rem;
    height: 1.2rem;
    margin-top: 0.1437rem;
  }

  .notice-text {
    width: 15.4375rem;
    height: 2.4375rem;
    font-size: 0.75rem;
    margin-left: 0.475rem;
    text-align: left;

    line-height:1.3125rem;
  }
`;
